html {
  margin: 0 auto;
  font-size: 16px;
}

body {
  background-color: #f4f4f4;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

*, :before, :after {
  box-sizing: border-box;
}

.container {
  max-width: 90%;
  margin: 0 auto;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #1c5a8d;
  border: none;
  border-radius: 4px;
  padding: .67rem 1rem;
}

button:hover {
  background-color: #023d77;
}

h1 {
  color: #fff;
  background-color: #1c5a8d;
  margin-top: 0;
  padding: 1.5rem;
}

.form-container {
  border-bottom: #333;
  font-size: .95rem;
  box-shadow: 0 0 4px 8px #9999990d;
}

.form-title {
  margin-bottom: .25rem;
  font-size: .8em;
}

.form-input-container {
  flex-direction: column;
  gap: .67rem;
  margin: 1.3rem 0;
  padding-bottom: 1.3rem;
  display: flex;
}

label {
  text-align: left;
  margin-bottom: .3rem;
  font-weight: bold;
}

input[type="text"], select {
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #999;
  border-radius: 4px;
  width: 100%;
  padding: 10px;
  transition: box-shadow .3s, border-color .3s;
  box-shadow: 0 0 4px 8px #9999990d;
}

.book-rating-input {
  justify-content: center;
  align-items: center;
  gap: .67rem;
  margin-top: .5rem;
  display: flex;
}

.book-rating-input input[type="number"] {
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 30%;
  margin-right: 5px;
  padding: 8px;
  transition: box-shadow .3s, border-color .3s;
}

input[type="text"]:focus, select:focus, input[type="text"]:hover, select:hover, input[type="number"]:hover, input[type="number"]:focus {
  border-color: #023d77;
  outline: none;
  box-shadow: 0 0 6px 12px #023d7726;
}

input::placeholder {
  color: #595959;
}

.add-book-btn {
  font-size: 1.2rem;
}

.bookshelf-container {
  padding: 0;
  font-size: .95rem;
  list-style: none;
}

.book {
  background-color: #fcfcfc;
  border: 1px solid #ddd;
  border-radius: 8px;
  margin-bottom: .67rem;
  padding: 1rem 1.25rem;
  list-style: none;
  box-shadow: 0 2px 5px #0000001a;
}

.book-header {
  justify-content: flex-start;
  align-items: baseline;
  gap: 1rem;
  margin: .5rem 0;
  display: flex;
}

.book-title {
  margin: 0;
  font-size: 1.2rem;
  font-weight: bold;
}

.book-author {
  color: #444;
  margin: 0;
  font-size: .9rem;
}

.book-genre {
  text-align: left;
  margin: .3rem 0;
  font-size: .8rem;
  font-style: italic;
}

.book-rating {
  margin: .5rem 0;
}

.read {
  font-size: 1.2rem;
  font-weight: bold;
}

.not-read {
  font-size: 1.05rem;
  font-style: italic;
}

.read, .not-read {
  margin-bottom: 0;
}

#chatbot-container {
  background: #fff;
  border: 1px solid #ccc;
  width: 300px;
  padding: 10px;
  position: fixed;
  bottom: 20px;
  right: 20px;
  box-shadow: 0 4px 6px #0000001a;
}

#chat-history {
  border-bottom: 1px solid #ccc;
  height: 200px;
  margin-bottom: 10px;
  padding: 5px;
  overflow-y: auto;
}

#chat-input {
  width: calc(100% - 60px);
  margin-right: 5px;
  padding: 5px;
}

#send-btn {
  cursor: pointer;
  padding: 5px 10px;
}

.history {
  padding-bottom: 15px;
}

@media (width >= 600px) {
  .container {
    max-width: 33rem;
  }

  .form-container, .bookshelf-container {
    font-size: 1rem;
  }

  input[type="text"], select, input[type="number"] {
    padding: 12px;
  }

  .book-title {
    font-size: 1.4rem;
  }

  .book-author {
    font-size: 1rem;
  }
}

@media (width >= 900px) {
  .container {
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
    gap: .67rem;
    max-width: 90%;
    display: flex;
  }

  .form-container, .bookshelf-container {
    flex: 0 1 1fr;
  }
}
